












































































export default {

	mounted() {
		uni.getStorage({
			key: "address",
			success: (data) => {
				console.log(data, data.data)
				this.addrList = JSON.parse(data.data)
				if (this.addrList.distance) {
					this.getgoods_name = "1";
				}
			}
		})
		//确认订单信息
		uni.request({
			url: this.config.url + "order/sure",
			method: "post",
			data: {
				token: this.token,
				goods: this.goods
			},
			success: (res) => {
				//console.log(res)
				if (res.data.code == 1) {
					this.buyList = res.data.data.goods;
					this.express = res.data.data.express;
					this.total = res.data.data.total;
					this.number = res.data.data.number;
				}
			}
		})
	},
	computed: {
		totalmoney() {
			var totalNum = Number(this.total) + Number(this.express)

			return totalNum
		}
	},
	data() {
		return {
			buyList: [], //订单列表
			goodsinfo: {},
			number: "", //购买商品总数量
			goodsPrice: 0.0, //商品合计价格
			sumPrice: 0.0, //用户付款价格
			freight: 12.00, //运费
			note: '', //备注
			int: 1200, //抵扣积分
			deduction: 0, //抵扣价格
			goods: [],
			express: '',
			total: "", //商品总价格
			addrList: {}, //地址列表
			getgoods_name: false, //送货类型显示
			order_no: "", //订单编号

		};
	},
	onShow() {

	},
	onHide() {

	},
	onLoad(option) {
		console.log(option)



		// 获取购买商品信息

		if (option.iscart == 0) {
			this.goodsinfo.goods_spec = option.size;

			this.goodsinfo.goods_number = option.num;
			this.goodsinfo.goods_id = option.id
			this.goods.push(this.goodsinfo)
		}
	},
	onBackPress() {
		//页面后退时候，清除订单信息
		this.clearOrder();
	},
	filters: {
		toFixed: function(x) {
			return parseFloat(x).toFixed(2);
		}
	},
	methods: {
		//选取送货方式
		sendType() {
			uni.navigateTo({
				url: "/pages/sendType/sendType"
			})
		},
		// 获取购买商品信息

		clearOrder() {
			uni.removeStorage({
				key: 'buylist',
				success: (res) => {
					this.buylist = [];
					//console.log('remove buylist success');
				}
			});
		},
		toPay() {
			//商品列表
			uni.request({
				url: this.config.url + "order/order",
				method: "post",
				data: {
					token: this.token,
					goods: this.goods,
					order_type: this.getgoods_name,
					address_id: this.addrList.id,
					from_car: 0
				},
				success: (res) => {
					console.log(res)
					if (res.data.code == 1) {
						this.order_no = res.data.data.order_no; //获取订单编号

						//调起支付接口
						uni.request({
							url: this.config.url + "order/pay",
							method: "POST",
							data: {
								token: this.token,
								order_no: this.order_no
							},
							success: (res) => {
								console.log(res)
								if (res.data.code == 1) {
									var pay = res.data.data.data
									uni.requestPayment({
										provider: 'wxpay',
										appid:pay.appId,
										timeStamp: pay.timeStamp,
										nonceStr: pay.nonceStr,
										package: pay.package,
										signType: pay.signType,
										paySign: pay.paySign,
										success: function(res) {
											console.log('success:' + JSON.stringify(res));
										},
										fail: function(err) {
											console.log('fail:' + JSON.stringify(err));
										}
									});
								}
							}
						})
					} else if (res.data.code == 0) {
						uni.showToast({
							title: "提交订单失败",
							duration: 1000
						})
					}
				}
			})
			//本地模拟订单提交UI效果
			// uni.showLoading({
			// 	title: '正在提交订单...'
			// })
			// setTimeout(() => {
			// 	uni.setStorage({
			// 		key: 'paymentOrder',
			// 		data: paymentOrder,
			// 		success: () => {
			// 			uni.hideLoading();
			// 			uni.redirectTo({
			// 				url: "../pay/payment/payment?amount=" + this.sumPrice
			// 			})
			// 			
			// 		}
			// 	})
			// }, 1000)

		}

	}
}
